import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Layout from '../views/layout/Layout'

export const routes = [
  { path: '/login', component: () => import ('@/views/login/index'), hidden: true },
  {
    path: '/',
    name: 'Dashboard',
    component: Layout,
    redirect: '/dashboard',
    hidden: true,
    children: [{
      path: 'dashboard',
      component: () => import ('@/views/dashboard/index')
    }]
  },
  {
    path: '/example',
    component: Layout,
    redirect: '/example/table',
    name: 'Example',
    meta: { title: 'Example', icon: '#icon-example' },
    children: [
      {
        path: 'table',
        name: 'Table',
        component: () => import ('@/views/table/index'),
        meta: { title: 'Table', icon: '#icon-table' }
      },
      {
        path: 'tree',
        name: 'Tree',
        component: () => import('@/views/tree/index'),
        meta: { title: 'Tree', icon: '#icon-tree' }
      }
    ]
  },
  {
    path: '/form',
    component: Layout,
    children: [
      {
        path: 'index',
        name: 'Form',
        component: () => import('@/views/form/index'),
        meta: { title: 'Form', icon: '#icon-form' }
      }
    ]
  },
  {
    path: '/nested',
    component: Layout,
    name: 'Nested',
    meta: { title: 'Nested', icon: '#icon-nested' },
    redirect: '/nested/menu1',
    children: [
      {
        path: 'menu1',
        name: 'Menu1',
        component: () => import('@/views/nested/menu1/index'),
        meta: { title: 'Menu1' },
        children: [
          {
            path: 'menu1-1',
            name: 'Menu1-1',
            component: () => import('@/views/nested/menu1/menu1-1/index'),
            meta: { title: 'Menu1-1' }
          },
          {
            path: 'menu1-2',
            name: 'Menu1-2',
            component: () => import('@/views/nested/menu1/menu1-2/index'),
            meta: { title: 'Menu1-2' },
            children: [
              {
                path: 'menu1-2-1',
                name: 'Menu1-2-1',
                component: () => import('@/views/nested/menu1/menu1-2/menu1-2-1/index'),
                meta: { title: 'Menu1-2-1' },
                children: [
                  {
                    path: 'menu1-2-1-1',
                    name: 'Menu1-2-1-1',
                    component: () => import('@/views/nested/menu1/menu1-2/menu1-2-1/menu1-2-1-1/index'),
                    meta: { title: 'Menu1-2-1-1' }
                  },
                  {
                    path: 'menu1-2-1-2',
                    name: 'Menu1-2-1-2',
                    component: () => import('@/views/nested/menu1/menu1-2/menu1-2-1/menu1-2-1-2/index'),
                    meta: { title: 'Menu1-2-1-2' }
                  }
                ]
              },
              {
                path: 'menu1-2-2',
                name: 'Menu1-2-2',
                component: () => import('@/views/nested/menu1/menu1-2/menu1-2-2/index'),
                meta: { title: 'Menu1-2-2' }
              }
            ]
          }
        ]
      },
      {
        path: 'menu2',
        name: 'Menu2',
        component: () => import('@/views/nested/menu2/index'),
        meta: { title: 'Menu2' }
      },
      {
        path: 'menu3',
        name: 'Menu3',
        component: () => import('@/views/nested/menu3/index'),
        meta: { title: 'Menu3' },
        children: [
          {
            path: 'menu3-1',
            name: 'Menu3-1',
            component: () => import('@/views/nested/menu3/menu3-1/index'),
            meta: { title: 'Menu3-1' },
            children: [
              {
                path: 'menu3-1-1',
                name: 'Menu3-1-1',
                component: () => import('@/views/nested/menu3/menu3-1/menu3-1-1/index'),
                meta: { title: 'Menu3-1-1' },
              }
            ]
          }
        ]
      }
    ]
  },
  {
    path: 'external-link',
    component: Layout,
    children: [
      {
        // path: 'https://www.baidu.com',
        path: 'https://panjiachen.github.io/vue-element-admin-site/#/',
        meta: { title: 'External Link', icon: '#icon-external-link' }
      }
    ]
  }
  
]

const router = new VueRouter({
  routes
})

export default router
