import request from '@/utils/request'

export function login(username, password) {
  console.log('api/login');
  return request({
    url: '/user/login',
    method: 'post',
    data: {
      username,
      password
    }
  })
}

export function getInfo(token) {
  console.log('进入getINFO'+localStorage.getItem('admin'));
  return request({
    url: '/user/info',
    method: 'get',
    params: { token }
  })
}

export function logout() {
  return request({
    url: '/user/logout',
    method: 'post'
  })
}