<script>
export default {
  name: 'MenuItem',
  functional: true,
  props: {
    meta: {
      type: Object,
      default: () => {
        return {
          title: '',
          icon: ''
        }
      }
    }
  },
  render(h, context) {
    const { icon, title } = context.props.meta;
    const vnodes = [];
    
    if(icon) {
      vnodes.push(
                 <svg class="icon" aria-hidden="true">
                 <use href={icon}>
                 </use>
                 </svg>
                 )
    }
    
    if(title) {
      vnodes.push(<span slot='title'>{(title)}</span>)
    }
    return vnodes;
  }
}
</script>

