import router from './router'
import store from './store'
import NProgress from 'nprogress' //    progress bar
import 'nprogress/nprogress.css' // progress bar style
import { Message } from 'element-ui'
import { getUserName } from '@/utils/auth' // get Token from cookie

NProgress.configure({ showSpinner: false }) // NProgress configuration

const whiteList = ['/login']  //不重定向白名单
// 若已存在username则继续判断路径
router.beforeEach((to, from, next) => { 
  NProgress.start();
  // 判断localStorage是否存在username
  if(getUserName()) {
    // 已经存在username 若to.path为login 则直接转跳根路径
    if(to.path === '/login') {
      next({ path: '/' });
      NProgress.done();
    } else {
      // to.path不为login 则判断是否存在roles 没有则拉取用户信息
      if(store.getters.roles.length === 0) {
        // 拉取用户信息
        store.dispatch('GetInfo').then(() => {
        // 拉取成功则转跳
        next();
        }).catch((err) => {
          // 拉取信息失败则自动登出
          store.dispatch('FedLogOut').then(() => {
            Message.error(err || 'Verificationfailed, please login again');
            next({ path: '/' });
          })
        })
      } else {
        // 存在roles则直接跳转
        next();
      }
    }
  } else {  // getUserName false
    // 不存在则继续判断to是否为login
    if(whiteList.indexOf(to.path) !== -1) {
      // 如果路径为login则前往login界面
      next();
    } else {
      // 若localStorage的username为空且to不为login 则进入/login 并且将当前路径设置为预选路径
      next(`/login?redirect=${to.path}`);
      NProgress.done();
    }
  }
})

router.afterEach(() => {
  NProgress.done();
})
